.container{
  display: flex;
  justify-content: center; /* Center horizontally */
  flex-direction: column;
  align-items: center; /* Center vertically */
  height: 100vh; /* Full height of the viewport */
  padding-left: 10%;
  padding-right: 10%;
  gap: 10%;
}
.title{
  font-size: x-large;
}
.link{
  display: block; /* Make the link fill the entire content box */
  padding: 20px;
  border-radius: 10%;
  background-color: #E7F6CE;
  font-size: large;
}
